dow:-4px 4px 15px inset rgba(0,0,0,.4);transform:translate(74px, -3px);cursor:pointer}.theme-switch__label::before,.theme-switch__label::after{font-size:2rem;position:absolute;transform:translate3d(0, -50%, 0);top:50%}.theme-switch__label::before{content:"☼";right:100%;margin-right:10px;color:orange}.theme-switch__label::after{content:"☾";left:100%;margin-left:10px;color:#789;transform:translateY(-24px)}.theme-switch__label span{position:absolute;bottom:calc(100% + 10px);left:0;width:100%}.theme-switch__label span::after{position:absolute;top:calc(100% + 12px);left:4px;width:25px;height:25px;content:"";border-radius:50%;background-color:#add8e6;transition:transform 200ms,background-color 200ms;box-shadow:-3px 3px 8px rgba(0,0,0,.4)}.theme-switch__input:checked~.theme-switch__label{background-color:#789}.theme-switch__input:checked~.theme-switch__label::before{color:#789}.theme-switch__input:checked~.theme-switch__label::after{color:var(--violet-color)}.theme-switch__input:checked~.theme-switch__label span::after{transform:translate3d(46px, 0, 0)}@media screen and (max-width: 1084px){.main{padding:0 50px 60px}}@media screen and (max-width: 834px){.main{padding:100px 50px 60px}.theme-switch__label{transform:translate(55px, -3px)}}@media screen and (max-width: 424px){.main{padding:100px 10px 60px}}